<template>
  <a-row>
    <a-col :span="12">
      <SelectSearch
        ref="table"
        v-model="tableId"
        tableName="table"
        :params="tableSearchParams"
        :placeholder="languageBase.please_select"
        @change="handleTableChange"
      />
    </a-col>
    <a-col :span="12">
      <SelectSearch
        v-model="columnId"
        tableName="table_column"
        :show-search="showSearch"
        :params="columnSearchParams"
        :placeholder="languageBase.please_select"
        :disabled="!columnSearchParams"
        @selected="columnSelected"
      />
    </a-col>
  </a-row>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import SelectSearch from "./SelectSearch.vue";

export default {
  components: {
    SelectSearch
  },
  props: {
    value: [Number, String]
  },
  data() {
    let columnSearchParams = undefined;

    if (this.value) {
      columnSearchParams = {
        table: { fields: ["table"] }
      };
    }
    return {
      tableId: undefined,
      columnId: this.value,
      columnSearchParams,
      showSearch: this.value === undefined,
      tableSearchParams: {
        table: {
          filters: [
            [
              ["state", "=", "1"],
              ["id", ">", "12"]
            ]
          ]
        }
      }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    value(val) {
      if (this.columnId === val) return;

      if (val === undefined) {
        this.tableId = undefined;
        this.columnSearchParams = undefined;
        this.columnId = undefined;
      } else {
        this.columnSearchParams = {
          table: { fields: ["table"] }
        };
        this.showSearch = false;
        this.$nextTick(() => {
          this.columnId = val;
        });
      }
    },
    columnId(val) {
      if (this.value === val) return;

      if (this.columnId !== undefined) {
        this.$emit("input", val);
        this.$emit("change", val);
      } else {
        this.handleColumnSearchParams();
      }
    }
  },
  methods: {
    handleTableChange() {
      this.columnId = undefined;
      this.handleColumnSearchParams();
    },
    handleColumnSearchParams() {
      this.showSearch = true;
      if (this.tableId === undefined) {
        this.columnSearchParams = undefined;
      } else if (
        this.tableId !==
        _.get(this.columnSearchParams, "table.filters[0][0][2]")
      ) {
        this.columnSearchParams = {
          table: {
            filters: [
              [
                ["table", "=", this.tableId],
                ["state", "=", "1"]
              ]
            ]
          }
        };
      }
    },
    columnSelected(selectedItem) {
      if (this.tableId) return;

      const table = _.get(selectedItem, "table");
      if (table) {
        this.$refs.table.list.data = [table];
        this.tableId = table.id;
      }
    }
  }
};
</script>
