<template>
  <a-card :bordered="false" style="margin:16px 24px">
    <Edit ref="edit" />

    <Table
      create
      edit
      del
      storeModule="menu_group"
      :columns="columns"
      :filters="filters"
      @show-edit="data => $refs.edit.show(data)"
    >
      <template v-slot:icon="{ text }">
        <a-icon :type="text || 'menu'" />
      </template>
    </Table>
  </a-card>
</template>

<script>
import { mapState } from "vuex";
import Table from "@/components/Table.vue";
import Edit from "./Edit.vue";

export default {
  components: {
    Table,
    Edit
  },
  data() {
    return {
      filters: {
        name: { operator: "LIKE" }
      },
      columns: [
        {
          dataIndex: "name",
          sorter: true
        },
        {
          dataIndex: "icon",
          scopedSlots: { customRender: "icon" }
        },
        {
          dataIndex: "comment"
        },
        {
          dataIndex: "created_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          dataIndex: "state",
          scopedSlots: { customRender: "state" },
          sorter: true
        }
      ]
    };
  },
  computed: {
    ...mapState({
      languageTable: store => store.languageTable.values
    })
  },
  metaInfo() {
    return {
      title: this.languageTable.menu_group
    };
  }
};
</script>
