<template>
  <a-select v-model="val" showSearch allow-clear>
    <a-select-opt-group
      v-for="(items, key) in icons"
      :key="key"
      :label="title[key]"
    >
      <a-select-option v-for="icon in items" :key="icon" :value="icon">
        <a-icon :type="icon" />
        {{ icon }}
      </a-select-option>
    </a-select-opt-group>
  </a-select>
</template>

<script>
import { mapState } from "vuex";
import { titles, icons } from "./data";

export default {
  props: {
    value: String
  },
  data() {
    return {
      titles,
      icons,
      val: this.value
    };
  },
  computed: {
    ...mapState({
      localeId: store => store.locale.selected.id
    }),
    title() {
      return this.titles[this.localeId] || this.titles[50];
    }
  },
  watch: {
    value(val) {
      this.val = val;
    },
    val(val) {
      this.$emit("input", val);
    }
  }
};
</script>
