<template>
  <a-drawer
    :title="title"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    @close="close"
    :headerStyle="{ textAlign: 'center' }"
  >
    <a-form-model
      v-if="visible"
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item
        prop="name"
        :label="languageTableColumn['data_type.name'] || languageBase.name"
      >
        <a-input v-model="formData.name" auto-focus />
      </a-form-model-item>
      <a-form-model-item prop="comment" :label="languageBase.comment">
        <a-textarea v-model="formData.comment" />
      </a-form-model-item>
      <a-form-model-item
        prop="component"
        :label="languageTableColumn['data_type.component'] || 'component'"
      >
        <a-select v-model="formData.component" @change="handleComponentChange">
          <a-select-option
            v-for="component in componentOptions"
            :key="component"
            :value="component"
          >
            {{ component }}
          </a-select-option>
        </a-select>
      </a-form-model-item>

      <InputNumber
        v-if="formData.component === 'inputNumber'"
        v-model="formData.params"
      />
      <Textarea
        v-else-if="formData.component === 'textarea'"
        v-model="formData.params"
      />
      <a-form-model-item
        v-else-if="/^(radioSelect|cascader|radio)$/.test(formData.component)"
        prop="params.table_column_id"
        :label="languageBase.table + ' - ' + languageBase.field"
      >
        <SelectTableColumn v-model="formData.params.table_column_id" />
      </a-form-model-item>
      <Multiple
        v-else-if="/^(checkbox|multipleSelect)$/.test(formData.component)"
        v-model="formData.params"
      />
      <Date
        v-else-if="formData.component === 'datePicker'"
        v-model="formData.params"
      />
      <Time
        v-else-if="formData.component === 'timePicker'"
        v-model="formData.params"
      />

      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
          :disabled="disabledSubmit"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { exist } from "@/api";
import SelectTableColumn from "@/components/SelectTableColumn.vue";
import InputNumber from "./params/InputNumber.vue";
import Textarea from "./params/Textarea.vue";
import Multiple from "./params/Multiple.vue";
import Date from "./params/Date.vue";
import Time from "./params/Time.vue";

const defaultFormData = {
  name: "",
  comment: "",
  component: "",
  params: {}
};

export default {
  components: {
    InputNumber,
    Textarea,
    SelectTableColumn,
    Multiple,
    Date,
    Time
  },
  data() {
    const ruleInputRequired = {
      required: true,
      validator: (rule, value, callback) => {
        value === "" || value === undefined || value === null || _.isNaN(value)
          ? callback(this.languageBase.please_input)
          : callback();
      }
    };
    const ruleSelectRequired = {
      required: true,
      validator: (rule, value, callback) => {
        value === "" || value === undefined
          ? callback(this.languageBase.please_select)
          : callback();
      }
    };

    const validateNameExist = (rule, value, callback) => {
      if (validateNameExist.val === value || this.oldFormData.name === value) {
        callback();
      } else {
        validateNameExist.val = value;
        exist({ table_name: "data_type", filters: { name: value } }).then(
          res => {
            res ? callback(this.languageBase.existed) : callback();
          }
        );
      }
    };

    return {
      visible: false,
      title: "",
      width: document.body.offsetWidth < 768 ? document.body.offsetWidth : 768,
      submitLoding: false,
      formData: { ...defaultFormData },
      rules: {
        name: [
          ruleInputRequired,
          { min: 1, max: 32, trigger: "blur" },
          { validator: validateNameExist, trigger: "blur" }
        ],
        comment: [ruleInputRequired, { min: 1, max: 255, trigger: "blur" }],
        component: [ruleSelectRequired],
        "params.table_column_id": [ruleSelectRequired],
        "params.table_related_id": [ruleSelectRequired],
        "params.index": [ruleSelectRequired]
      },
      oldFormData: { ...defaultFormData },
      componentOptions: [
        "input",
        "inputNumber",
        "switch",
        "textarea",
        "radio",
        "radioSelect",
        "checkbox",
        "multipleSelect",
        "cascader",
        "rate",
        "datePicker",
        "timePicker",
        "upload",
        "avatar",
        "richText"
      ]
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values
    }),
    disabledSubmit() {
      let disabled = true;
      _.forEach(this.oldFormData, (value, key) => {
        disabled = _.isEqual(this.formData[key], value);
        return disabled;
      });
      return disabled;
    }
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm && this.$refs.ruleForm.clearValidate();
    }
  },
  mounted() {
    this.handleRules();
  },
  methods: {
    ...mapActions("data_type", {
      save: "save"
    }),
    show(data) {
      if (data) {
        this.title = this.languageBase.edit;
        this.formData = _.pick(data, [
          "id",
          "name",
          "comment",
          "component",
          "params"
        ]);
      } else {
        this.title = this.languageBase.create;
        this.formData = { ...defaultFormData };
      }

      this.rules.name[2].validator.val = "";
      this.oldFormData = _.cloneDeep(
        _.pick(this.formData, _.keys(defaultFormData))
      );
      this.visible = true;
      this.$nextTick(() => {
        this.$refs.ruleForm.clearValidate();
      });
    },
    close() {
      this.$refs.ruleForm.clearValidate();
      this.visible = false;
    },
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;

        let formData = _.cloneDeep(
          _.pick(this.formData, [
            "id",
            "name",
            "comment",
            "component",
            "params"
          ])
        );

        formData = _.pickBy(
          formData,
          (value, key) => !_.isEqual(value, this.oldFormData[key])
        );

        if (_.isEmpty(formData)) return;

        if (formData.params) {
          formData.params = JSON.stringify(formData.params);
        } else if (!formData.id) {
          formData.params = "{}";
        }

        this.save(formData).then(() => {
          this.$notification.success({
            message: this.languageBase.save,
            comment: this.languageBase.successful
          });
          this.close();
        });
      });
    },
    handleRules() {
      const compiled = _.template(this.languageBase.character_template);
      this.rules.name[1].message = compiled(this.rules.name[1]);
      this.rules.comment[1].message = compiled(this.rules.comment[1]);
    },
    handleComponentChange(val) {
      if (this.formData.id && val === this.oldFormData.component) {
        this.formData.params = _.cloneDeep(this.oldFormData.params);
      } else if (val === "inputNumber") {
        this.formData.params = { precision: 0, step: 1 };
      } else if (val === "textarea") {
        this.formData.params = { autoSize: { minRows: 2 } };
      } else if (/^(radioSelect|cascader|radio)$/.test(val)) {
        this.formData.params = { table_column_id: undefined };
      } else if (/^(datePicker|timePicker)$/.test(val)) {
        this.formData.params = { format: "" };
      } else {
        this.formData.params = {};
      }
    }
  }
};
</script>
