<template>
  <a-drawer
    :title="title"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    @close="close"
    :headerStyle="{ textAlign: 'center' }"
  >
    <a-form-model
      v-if="visible"
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item prop="comment" :label="languageBase.comment">
        <a-textarea
          auto-focus
          v-model="formData.comment"
          :auto-size="{ minRows: 1 }"
        />
      </a-form-model-item>

      <a-form-model-item
        prop="t1_column"
        :label="languageBase.table + ' 1 - ' + languageBase.field"
      >
        <SelectTableColumn v-model="formData.t1_column" />
      </a-form-model-item>

      <a-form-model-item
        prop="t2_column"
        :label="languageBase.table + ' 2 - ' + languageBase.field"
      >
        <SelectTableColumn v-model="formData.t2_column" />
      </a-form-model-item>

      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
          :disabled="disabledSubmit"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import SelectTableColumn from "@/components/SelectTableColumn.vue";

const defaultFormData = {
  comment: "",
  t1_column: undefined,
  t2_column: undefined
};

export default {
  components: {
    SelectTableColumn
  },
  data() {
    const validateInputRequired = (rule, value, callback) => {
      value === "" ? callback(this.languageBase.please_input) : callback();
    };

    const validateSelectRequired = (rule, value, callback) => {
      value === "" || value === undefined
        ? callback(this.languageBase.please_select)
        : callback();
    };

    const validateQuotationMarks = (rule, value, callback) => {
      /`|'/.test(value)
        ? callback(this.languageBase.no_quotation_marks)
        : callback();
    };

    return {
      visible: false,
      title: "",
      width: document.body.offsetWidth < 768 ? document.body.offsetWidth : 768,
      submitLoding: false,
      formData: { ...defaultFormData },
      rules: {
        comment: [
          { required: true, validator: validateInputRequired },
          { min: 1, max: 255, trigger: "blur" },
          { validator: validateQuotationMarks }
        ],
        t1_column: [{ required: true, validator: validateSelectRequired }],
        t2_column: [{ required: true, validator: validateSelectRequired }]
      },
      oldFormData: { ...defaultFormData }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values
    }),
    disabledSubmit() {
      return _.isEmpty(
        _.pickBy(this.oldFormData, (value, key) => this.formData[key] !== value)
      );
    }
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm && this.$refs.ruleForm.clearValidate();
    }
  },
  mounted() {
    this.handleRules();
  },
  methods: {
    ...mapActions("table_related", {
      save: "save"
    }),
    show(data) {
      if (data) {
        this.title = this.languageBase.edit;
        this.formData = {
          ..._.pick(data, ["id", "comment"]),
          t1_column: data.t1_column.id,
          t2_column: data.t2_column.id
        };
      } else {
        this.title = this.languageBase.create;
        this.formData = { ...defaultFormData };
      }

      this.oldFormData = _.pick(this.formData, _.keys(defaultFormData));
      this.visible = true;
      this.$nextTick(() => {
        this.$refs.ruleForm.clearValidate();
      });
    },
    close() {
      this.visible = false;
      this.$refs.ruleForm.clearValidate();
    },
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;

        let formData = _.pick(this.formData, [
          "id",
          "comment",
          "t1_column",
          "t2_column"
        ]);
        formData = _.pickBy(
          formData,
          (value, key) => value !== this.oldFormData[key]
        );

        if (_.isEmpty(formData)) return;

        this.save(formData).then(() => {
          this.$notification.success({
            message: this.languageBase.save,
            comment: this.languageBase.successful
          });
          this.close();
        });
      });
    },
    handleRules() {
      const compiled = _.template(this.languageBase.character_template);
      this.rules.comment[1].message = compiled(this.rules.comment[1]);
    }
  }
};
</script>
