import _ from "lodash";
import { storeOptions } from "./default";
import { queryAll, save } from "@/api";

const tableName = "language_table";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "table", "native_name"]
      }
    }
  },
  save: {
    api: save,
    tableName
  }
});

options.state = {
  ...options.state,
  values: {
    data_type: "数据类型",
    locale: "区域",
    member: "成员",
    menu_group: "菜单组",
    role: "角色",
    table: "数据表",
    table_column: "数据表的列",
    table_related: "多对多关联表"
  }
};

options.mutations = {
  ...options.mutations,
  setValues(state, values) {
    state.values = {
      ...state.values,
      ...values
    };
  }
};

options.actions = {
  ...options.actions,
  get({ commit, dispatch }, localeId) {
    dispatch("getAll", {
      table: { filters: [[["locale", "=", localeId]]] }
    }).then(({ data }) => {
      const values = {};
      _.forEach(data, item => {
        values[item.table.name] = item.native_name;
      });
      commit("setValues", values);
    });
  }
};

export default {
  namespaced: true,
  ...options
};
