<template>
  <div>
    <Filters
      v-if="filters"
      :tableName="storeModule"
      :items="filters"
      :loading="list.loading"
      @submit="handleFilters"
    />

    <TableBase
      :tableName="storeModule"
      :columns="columns"
      :list="list"
      :create="create"
      :edit="edit"
      :del="del"
      @change="handleChange"
      @state-change="handleStateChange"
      @delete="handleDelete"
      @show-edit="data => $emit('show-edit', data)"
    >
      <template
        v-for="(item, key) in $scopedSlots"
        v-slot:[key]="{ text, record, index }"
      >
        <span :key="key">
          <slot
            :name="key"
            v-bind:text="text"
            v-bind:record="record"
            v-bind:index="index"
          />
        </span>
      </template>
    </TableBase>
  </div>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import Filters from "./Filters";
import TableBase from "./TableBase";

export default {
  components: {
    Filters,
    TableBase
  },
  props: {
    storeModule: String,
    columns: Array,
    params: Object,
    filters: Object,
    create: Boolean,
    edit: Boolean,
    del: Boolean
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      list(store) {
        return store[this.storeModule].list;
      }
    }),
    getListActionName() {
      return this.storeModule + "/getList";
    },
    saveActionName() {
      return this.storeModule + "/save";
    }
  },
  watch: {
    params() {
      this.getList({ current: 1, pageSize: 10 });
    }
  },
  created() {
    this.getList({ current: 1, pageSize: 10 });
  },
  methods: {
    handleStateChange(record) {
      this.$store
        .dispatch(this.saveActionName, {
          id: record.id,
          state: !record.state
        })
        .then(() => {
          this.$emit("save", record);
        });
    },
    handleDelete(record) {
      this.$store
        .dispatch(this.saveActionName, {
          id: record.id,
          deleted_at: 1
        })
        .then(() => {
          this.$notification.success({
            message: this.languageBase.delete,
            description: this.languageBase.successful
          });
          this.$emit("delete", record);
        });
    },
    getList(params) {
      params = _.mergeWith(_.cloneDeep(this.params), params);

      if (_.isEmpty(_.get(params, "table.order_by"))) {
        _.set(params, "table.order_by", [["id", "DESC"]]);
      }

      if (!_.isEmpty(this.handleFilters.filters)) {
        const filters = _.get(params, "table.filters");
        if (_.isEmpty(filters)) {
          _.set(params, "table.filters[0]", this.handleFilters.filters);
        } else {
          _.forEach(filters, (value, index) => {
            _.set(params, `table.filters[${index}]`, [
              ...value,
              ...this.handleFilters.filters
            ]);
          });
        }
      }

      this.$store.dispatch(this.getListActionName, params);
    },
    handleChange(pagination, filters, sorter) {
      const params = _.pick(pagination, ["current", "pageSize"]);

      if (!_.isEmpty(sorter) && sorter.order) {
        params.table = {
          order_by: [[sorter.field, sorter.order === "ascend" ? "ASC" : "DESC"]]
        };
      }

      this.getList(params);
    },
    handleFilters(filters) {
      this.handleFilters.filters = filters;
      this.getList({ current: 1, pageSize: 10 });
    }
  }
};
</script>
