<template>
  <a-card :bordered="false" style="margin:16px 24px">
    <Field ref="column" />

    <Edit ref="edit" />

    <Table
      create
      edit
      storeModule="table"
      :params="params"
      :columns="columns"
      :filters="filters"
      @show-edit="data => $refs.edit.show(data)"
    >
      <template v-slot:operation="{ record }">
        <a-button icon="eye" @click="$refs.column.show(record)">
          {{ languageBase.field }}
        </a-button>
      </template>
      <template v-slot:icon="{ text }">
        <a-icon :type="text || 'table'" />
      </template>
    </Table>
  </a-card>
</template>

<script>
import { mapState } from "vuex";
import Table from "@/components/Table.vue";
import Edit from "./Edit.vue";
import Field from "./field/List.vue";

export default {
  components: {
    Table,
    Edit,
    Field
  },
  data() {
    return {
      params: {
        table: { filters: [[["id", ">", 10]]] }
      },
      filters: {
        name: { operator: "LIKE" },
        state: {}
      },
      columns: [
        {
          title: "ID",
          dataIndex: "id",
          sorter: true
        },
        {
          title: "Name",
          dataIndex: "name",
          sorter: true
        },
        {
          title: "Comment",
          dataIndex: "comment"
        },
        {
          title: "Icon",
          dataIndex: "icon",
          scopedSlots: { customRender: "icon" }
        },
        {
          key: "menu_group",
          dataIndex: "menu_group.name"
        },
        {
          title: "Created At",
          dataIndex: "created_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "Updated At",
          dataIndex: "updated_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "State",
          dataIndex: "state",
          scopedSlots: { customRender: "state" },
          sorter: true
        }
      ]
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  metaInfo() {
    return {
      title: this.languageTable.table
    };
  }
};
</script>
