import { queryAll, queryList, save } from "@/api";
import { storeOptions } from "./default";

const tableName = "table_column";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "name"]
      }
    }
  },
  list: {
    api: queryList,
    params: {
      table: {
        name: tableName,
        fields: [
          "name",
          "comment",
          "data_type",
          "min",
          "max",
          "null",
          "auth",
          "default",
          "index"
        ]
      }
    }
  },
  save: {
    api: save,
    tableName
  }
});

export default {
  namespaced: true,
  ...options
};
