import _ from "lodash";
import moment from "moment";
import Vue from "vue";
import { origins } from "@/dict";

_.forEach(
  {
    unixFormat: (value, format = "LLL") =>
      value ? moment.unix(value).format(format) : "",
    accountF: path => origins.ACCOUNT_FRONTEND + path,
    image: hash =>
      `${origins.OSS}/public/${hash.replace(/^(.{2})(.{2})/, "$1/$2/")}`
  },
  (filter, key) => {
    Vue.filter(key, (data, ...agm) => filter(data, ...agm));
  }
);
