<template>
  <a-row type="flex" justify="space-between">
    <a-col :span="11">
      <a-form-model-item :label="languageBase.precision">
        <a-input-number
          v-model="value.precision"
          :min="0"
          :max="308"
          :precision="0"
          style="width: 100%;"
        />
      </a-form-model-item>
    </a-col>
    <a-col :span="11">
      <a-form-model-item :label="languageBase.input_number_step">
        <a-input-number v-model="value.step" :min="0" style="width: 100%;" />
      </a-form-model-item>
    </a-col>
  </a-row>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";

export default {
  props: {
    value: Object
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    "value.precision"(val) {
      if (!_.isNumber(val) || this.value.precision < 0) {
        this.value.precision = 0;
      } else if (this.value.precision > 308) {
        this.value.precision = 308;
      }
    },
    "value.step"(val) {
      if (!_.isNumber(val) || this.value.step < 0) {
        this.value.step = 1;
      }
    }
  }
};
</script>
