<template>
  <a-form-model :label-col="{ span: 6 }" :wrapper-col="{ span: 15 }">
    <a-form-model-item
      v-for="(value, key) in languageBase"
      :key="key + value"
      :label="key"
    >
      <a-input
        :default-value="value"
        @blur="e => handleBlur(key, e.target.value)"
      />
    </a-form-model-item>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";

export default {
  computed: {
    ...mapState({
      selectedLocale: store => store.locale.selected,
      languageBase: store => store.languageBase.values,
      languageBaseAllData: store => store.languageBase.all.data
    })
  },
  methods: {
    ...mapActions("languageBase", {
      save: "save"
    }),
    handleBlur(key, native_name) {
      const oldData = _.find(this.languageBaseAllData, { key });
      if (oldData.native_name === native_name) return;
      this.save({ id: oldData.id, native_name }).then(() => {
        this.$notification.success({
          message: this.languageBase.save,
          description: this.languageBase.successful
        });
      });
    }
  }
};
</script>
