<template>
  <a-form-model-item :label="languageBase.format">
    <a-select v-model="value.format">
      <a-select-option v-for="value in formats" :key="value" :value="value">
        {{ nowUnix | unixFormat(value) }}
      </a-select-option>
    </a-select>
  </a-form-model-item>
</template>

<script>
import moment from "moment";
import { mapState } from "vuex";

export default {
  props: {
    value: Object
  },
  data() {
    return {
      nowUnix: moment().unix(),
      formats: ["LT", "LTS"]
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  }
};
</script>
