<template>
  <a-form-model-item :label="languageBase.textarea_row">
    <a-input-number v-model="value.autoSize.minRows" :min="1" :precision="0" />
  </a-form-model-item>
</template>

<script>
import { mapState } from "vuex";

export default {
  props: {
    value: Object
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    "value.autoSize.minRows"(val) {
      if (!val) {
        this.value.autoSize.minRows = 1;
      }
    }
  }
};
</script>
