<template>
  <a-drawer
    :title="title"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    @close="close"
    :headerStyle="{ textAlign: 'center' }"
  >
    <a-form-model
      v-if="visible"
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item
        prop="name"
        :label="languageTableColumn['table_column.name'] || languageBase.name"
      >
        <a-input v-model="formData.name" auto-focus />
      </a-form-model-item>
      <a-form-model-item prop="comment" :label="languageBase.comment">
        <a-textarea v-model="formData.comment" />
      </a-form-model-item>
      <a-form-model-item
        prop="data_type"
        :label="languageTableColumn['table_column.data_type'] || 'Data type'"
      >
        <SelectSearch
          v-model="formData.data_type"
          tableName="data_type"
          :placeholder="languageBase.please_select"
          :params="{
            table: {
              fields: ['component', 'params'],
              filters: [
                [
                  ['id', '>', 5],
                  ['state', '=', '1']
                ]
              ]
            }
          }"
          @selected="handleDataTypeSelected"
        />
      </a-form-model-item>
      <a-row v-if="showRange" type="flex" justify="space-between">
        <a-col :span="11">
          <a-form-model-item
            prop="min"
            :label="languageTableColumn['table_column.min'] || 'Min'"
          >
            <a-input-number
              v-model="formData.min"
              style="width: 100%;"
              @change="handleMinChange"
            />
          </a-form-model-item>
        </a-col>
        <a-col :span="11">
          <a-form-model-item
            prop="max"
            :label="languageTableColumn['table_column.max'] || 'Max'"
          >
            <a-input-number v-model="formData.max" style="width: 100%;" />
          </a-form-model-item>
        </a-col>
      </a-row>
      <!-- <a-form-model-item
        v-if="formData.data_type"
        :label="languageTableColumn['table_column.default'] || 'Default'"
      >
        <a-input v-model="formData.default" />
      </a-form-model-item> -->
      <!-- <a-form-model-item
        v-if="showNull"
        :label="languageTableColumn['table_column.null'] || 'Null'"
      >
        <a-switch v-model="formData.null" />
      </a-form-model-item> -->
      <a-form-model-item
        prop="index"
        :label="languageTableColumn['table_column.index'] || 'Index'"
      >
        <a-input-number v-model="formData.index" />
      </a-form-model-item>
      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
          :disabled="disabledSubmit"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";
import { exist } from "@/api";
import SelectSearch from "@/components/SelectSearch.vue";

const defaultFormData = {
  name: "",
  comment: "",
  default: "",
  data_type: undefined,
  null: false,
  min: null,
  max: null,
  index: null
};
const formDataKeys = _.keys(defaultFormData);

export default {
  components: {
    SelectSearch
  },
  props: {
    table: Object
  },
  data() {
    const ruleInputRequired = {
      required: true,
      validator: (rule, value, callback) => {
        value === "" || value === undefined || value === null || _.isNaN(value)
          ? callback(this.languageBase.please_input)
          : callback();
      }
    };
    const ruleSelectRequired = {
      required: true,
      validator: (rule, value, callback) => {
        value === "" || value === undefined
          ? callback(this.languageBase.please_select)
          : callback();
      }
    };
    const ruleQuotationMarks = {
      validator: (rule, value, callback) => {
        /`|'/.test(value)
          ? callback(this.languageBase.no_quotation_marks)
          : callback();
      }
    };
    const ruleNameExist = {
      trigger: "blur",
      validator: (rule, value, callback) => {
        if (
          ruleNameExist.validator.val === value ||
          this.oldFormData.name === value
        ) {
          callback();
        } else {
          ruleNameExist.validator.val = value;
          exist({
            table_name: "table_column",
            filters: { name: value, table: this.table.id }
          }).then(res => {
            res ? callback(this.languageBase.existed) : callback();
          });
        }
      }
    };

    return {
      visible: false,
      title: "",
      width: document.body.offsetWidth < 768 ? document.body.offsetWidth : 768,
      submitLoding: false,
      showRange: false,
      showNull: false,
      formData: { ...defaultFormData },
      rules: {
        name: [
          ruleInputRequired,
          { min: 1, max: 32, trigger: "blur" },
          ruleQuotationMarks,
          ruleNameExist
        ],
        comment: [
          ruleInputRequired,
          { min: 1, max: 255, trigger: "blur" },
          ruleQuotationMarks
        ],
        min: [ruleInputRequired],
        max: [ruleInputRequired],
        data_type: [ruleSelectRequired],
        index: [{ type: "number", min: 0, max: 255, trigger: "blur" }]
      },
      oldFormData: { ...defaultFormData }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values,
      languageTableColumnAllData: store => store.languageTableColumn.all.data
    }),
    disabledSubmit() {
      return _.isEmpty(
        _.pickBy(this.oldFormData, (value, key) => this.formData[key] !== value)
      );
    }
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm && this.$refs.ruleForm.clearValidate();
    }
  },
  mounted() {
    this.handleRules();
  },
  methods: {
    ...mapMutations("languageTableColumn", {
      setLanguageTableColumnValues: "setValues",
      setLanguageTableColumnItem: "setItem"
    }),
    ...mapActions("table_column", {
      save: "save"
    }),
    show(data) {
      if (data) {
        this.title = this.languageBase.edit;
        this.formData = _.pick(data, [...formDataKeys, "id"]);
        this.formData.data_type = this.formData.data_type.id;
        this.formData.null = !!this.formData.null;
      } else {
        this.title = this.languageBase.create;
        this.formData = { ...defaultFormData };
      }

      this.rules.name[3].validator.val = "";
      this.oldFormData = _.pick(this.formData, formDataKeys);
      this.visible = true;
      this.$nextTick(() => {
        this.$refs.ruleForm.clearValidate();
      });
    },
    close() {
      this.visible = false;
      this.$refs.ruleForm.clearValidate();
    },
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;

        let formData = _.pick(this.formData, [...formDataKeys, "id"]);

        if (!formData.id) {
          formData.table = this.table.id;
        } else {
          formData = _.pickBy(
            formData,
            (value, key) => value !== this.oldFormData[key]
          );
          if (_.isEmpty(formData)) return;
        }

        this.save(formData).then(() => {
          if (formData.id && formData.name) {
            _.forOwn(sessionStorage, (value, key) => {
              /^q-language_table_column-/.test(key) &&
                sessionStorage.removeItem(key);
            });
            this.setLanguageTableColumnValues({
              ...this.languageTableColumn,
              [`${this.table.name}.${formData.name}`]: this.languageTableColumn[
                `${this.table.name}.${this.oldFormData.name}`
              ]
            });

            const item = _.find(this.languageTableColumnAllData, {
              table: { name: this.table.name },
              table_column: { name: this.oldFormData.name }
            });
            if (item) {
              this.setLanguageTableColumnItem({
                id: item.id,
                table_column: { name: formData.name }
              });
            }
          }

          this.$notification.success({
            message: this.languageBase.save,
            comment: this.languageBase.successful
          });

          this.close();
        });
      });
    },
    handleRules() {
      const compiled = _.template(this.languageBase.character_template);
      this.rules.name[1].message = compiled(this.rules.name[1]);
      this.rules.comment[1].message = compiled(this.rules.comment[1]);
    },
    handleDataTypeSelected(item) {
      this.showRange = false;
      this.showNull = true;
      this.formData.min = defaultFormData.min;
      this.formData.max = defaultFormData.max;
      this.formData.null = defaultFormData.null;
      this.formData.default = defaultFormData.default;

      if (_.isEmpty(item)) return;

      let min, max;

      if (/^(input|textarea|richText)$/.test(item.component)) {
        min = 0;
        max = 65535;
        this.showRange = true;
      } else if (item.component === "inputNumber") {
        if (_.isString(item.params)) {
          item.params = JSON.parse(item.params);
        }

        if (item.params.precision == 0) {
          min = Number.MIN_SAFE_INTEGER;
          max = Number.MAX_SAFE_INTEGER;
        } else {
          min = -Number.MAX_VALUE;
          max = Number.MAX_VALUE;
        }
        this.showRange = true;
      } else if (item.component === "switch") {
        this.formData.min = 0;
        this.formData.max = 1;
        this.showNull = false;
      } else if (
        /^(radio|radioSelect|checkbox|multipleSelect|cascader)$/.test(
          item.component
        )
      ) {
        this.formData.min = 0;
        this.formData.max = 99999999;
      } else if (item.component === "rate") {
        this.formData.min = 0;
        this.formData.max = 5;
      } else if (item.component === "datePicker") {
        this.formData.min = 0;
        this.formData.max = 4294967295;
      } else if (item.component === "avatar") {
        this.formData.min = 32;
        this.formData.max = 32;
      } else if (item.component === "upload") {
        this.formData.min = 0;
        this.formData.max = 0;
      }

      if (item && item.id === this.oldFormData.data_type) {
        this.formData.min = this.oldFormData.min;
        this.formData.max = this.oldFormData.max;
        this.formData.null = this.oldFormData.null;
        this.formData.default = this.oldFormData.default;
      }

      this.rules.min = [this.rules.min[0]];
      if (min !== undefined) {
        this.rules.min[1] = {
          type: "number",
          min,
          max,
          message: `${min} ~ ${max}`
        };
      }
    },
    handleMinChange(min) {
      if (!_.isEmpty(this.rules.min[1])) {
        this.rules.max[1] = {
          ...this.rules.min[1],
          min,
          message: `${min} ~ ${this.rules.min[1].max}`
        };
      }
    }
  }
};
</script>
