import _ from "lodash";
import { storeOptions } from "./default";
import { queryAll, save } from "@/api";

const tableName = "language_menu_group";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "menu_group", "native_name"]
      }
    }
  },
  save: {
    api: save,
    tableName
  }
});

options.state = {
  ...options.state,
  values: {
    member: "成员管理"
  }
};

options.mutations = {
  ...options.mutations,
  setValues(state, values) {
    state.values = {
      ...state.values,
      ...values
    };
  }
};

options.actions = {
  ...options.actions,
  get({ commit, dispatch }, localeId) {
    dispatch("getAll", {
      table: { filters: [[["locale", "=", localeId]]] }
    }).then(({ data }) => {
      const values = {};
      _.forEach(data, item => {
        values[item.menu_group.name] = item.native_name;
      });
      commit("setValues", values);
    });
  }
};

export default {
  namespaced: true,
  ...options
};
