import { queryAll, queryList, save } from "@/api";
import { storeOptions } from "./default";

const tableName = "table";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "name"]
      }
    }
  },
  list: {
    api: queryList,
    params: {
      table: {
        name: tableName,
        fields: ["name", "comment", "auth", "icon", "menu_group"]
      }
    }
  },
  save: {
    api: save,
    tableName
  }
});

export default {
  namespaced: true,
  ...options
};
