<template>
  <span @click="initList">
    <a-select
      allow-clear
      v-model="val"
      :show-search="showSearch"
      :default-active-first-option="false"
      :loading="fetching"
      :placeholder="placeholder"
      :disabled="disabled"
      :filter-option="false"
      @search="handleSearch"
      @popupScroll="popupScroll"
      style="width: 100%"
    >
      <a-select-option
        v-for="(item, index) in list.data"
        :key="index"
        :value="item[valueKey]"
      >
        {{ item[textKey] }}
      </a-select-option>
      <a-select-option v-if="fetching" value="fetching" disabled>
        <a-spin size="small" />
      </a-select-option>
    </a-select>
  </span>
</template>

<script>
import _ from "lodash";
import { queryList } from "@/api";

export default {
  props: {
    value: [Number, String],
    tableName: String,
    params: Object,
    textKey: {
      type: String,
      default: "name"
    },
    valueKey: {
      type: String,
      default: "id"
    },
    placeholder: String,
    disabled: Boolean,
    showSearch: {
      type: Boolean,
      default: true
    }
  },
  data() {
    this.handleSearch = _.debounce(this.handleSearch, 500);

    return {
      val: this.value,
      fetching: false,
      list: { data: [] }
    };
  },
  computed: {
    selected() {
      return _.find(this.list.data, { [this.valueKey]: this.val });
    }
  },
  watch: {
    value(val) {
      if (val !== this.val) {
        this.val = val;
        val !== undefined && this.fetchList(this.handleParams("", val));
      }
    },
    val(val) {
      if (val === undefined) {
        this.initList.initialized = false;
      }

      if (val !== this.value) {
        this.$emit("input", val);
        this.$emit("change", val, this.selected);
      }
    },
    params(val, old) {
      if (!_.isEqual(val, old)) {
        this.val = undefined;
        this.initList.initialized = false;
      }
    },
    selected(val) {
      this.$emit("selected", val);
    }
  },
  created() {
    this.value !== undefined &&
      this.fetchList(this.handleParams("", this.value));
  },
  methods: {
    fetchList(params) {
      this.fetching = true;
      this.initList.initialized = true;
      this.fetchList.params = params;

      queryList(params)
        .then(res => {
          if (params === this.fetchList.params) {
            if (res.current === 1) {
              this.list = {
                ...res,
                pages: _.ceil(res.total / res.pageSize)
              };
            } else {
              this.list = {
                ...this.list,
                current: res.current,
                data: [...this.list.data, ...res.data]
              };
            }
          }
        })
        .finally(() => {
          if (params === this.fetchList.params) {
            this.fetching = false;
          }
        });
    },
    handleParams(searchValue, initValue) {
      const params = _.mergeWith(
        {
          current: 1,
          table: {
            name: this.tableName,
            no_default_field: true,
            fields: [this.textKey, this.valueKey],
            order_by: [[this.textKey, "ASC"]]
          }
        },
        this.params,
        (objValue, srcValue) => {
          if (_.isArray(objValue)) {
            return objValue.concat(srcValue);
          }
        }
      );

      if (searchValue) {
        const filter = [this.textKey, "LIKE", `%${searchValue}%`];
        if (_.isEmpty(params.table.filters)) {
          params.table.filters = [[filter]];
        } else {
          _.forEach(params.table.filters, item => {
            item.push(filter);
          });
        }
      }

      if (initValue) {
        const filter = [this.valueKey, "=", initValue];
        if (_.isEmpty(params.table.filters)) {
          params.table.filters = [[filter]];
        } else {
          _.forEach(params.table.filters, item => {
            item.push(filter);
          });
        }
      }

      return params;
    },
    handleSearch(value) {
      this.fetchList(this.handleParams(value));
    },
    initList() {
      if (!this.val && !this.initList.initialized && !this.disabled) {
        this.list = { data: [] };
        this.initList.initialized = true;
        this.fetchList(this.handleParams());
      }
    },
    popupScroll(e) {
      const { scrollTop, clientHeight, scrollHeight } = e.target;
      if (
        scrollTop + clientHeight + 1 > scrollHeight &&
        this.list.pages > this.list.current
      ) {
        this.fetchList({
          ...this.fetchList.params,
          current: this.list.current + 1
        });
      }
    }
  }
};
</script>
