<template>
  <div>
    <Edit ref="edit" :table="table" />

    <a-modal
      :title="`${languageBase.table} ${table.name} - ${languageBase.field}`"
      :visible="visible"
      :footer="null"
      width="90vw"
      @cancel="visible = false"
    >
      <Table
        v-if="visible"
        storeModule="table_column"
        :params="params"
        :columns="columns"
        :filters="filters"
        create
        edit
        @show-edit="data => $refs.edit.show(data)"
      >
        <template v-slot:null="{ text, record }">
          <a-switch
            :disabled="record.auth === 1"
            :checked="!!text"
            @change="checked => save({ id: record.id, null: checked })"
          />
        </template>
      </Table>
    </a-modal>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Table from "@/components/Table.vue";
import Edit from "./Edit.vue";

export default {
  components: {
    Table,
    Edit
  },
  data() {
    return {
      visible: false,
      table: {},
      params: {},
      filters: {
        name: { operator: "LIKE" },
        state: {}
      },
      columns: [
        {
          title: "ID",
          dataIndex: "id",
          sorter: true
        },
        {
          title: "Name",
          dataIndex: "name",
          sorter: true
        },
        {
          title: "Comment",
          dataIndex: "comment"
        },
        {
          title: "Data type",
          key: "data_type",
          dataIndex: "data_type.name"
        },
        {
          title: "Min",
          dataIndex: "min"
        },
        {
          title: "Max",
          dataIndex: "max"
        },
        {
          title: "Default",
          dataIndex: "default"
        },
        {
          title: "Null",
          dataIndex: "null",
          scopedSlots: { customRender: "null" },
          sorter: true
        },
        {
          title: "Index",
          dataIndex: "index",
          sorter: true
        },
        {
          title: "Created At",
          dataIndex: "created_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "Updated At",
          dataIndex: "updated_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "State",
          dataIndex: "state",
          scopedSlots: { customRender: "state" },
          sorter: true
        }
      ]
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  methods: {
    ...mapActions("table_column", {
      save: "save"
    }),
    show(table) {
      this.table = table;
      this.params = {
        table: {
          filters: [[["table", "=", table.id]]]
        }
      };
      this.visible = true;
    }
  }
};
</script>
