<template>
  <a-form-model :label-col="{ span: 6 }" :wrapper-col="{ span: 15 }">
    <a-form-model-item :label="languageBase.table">
      <SelectSearch
        v-model="tableId"
        tableName="table"
        :placeholder="languageBase.please_select"
        @change="tableChange"
      />
    </a-form-model-item>
    <a-divider>{{ languageBase.field }}</a-divider>
    <template v-if="tableId">
      <a-form-model-item
        v-for="item in languages"
        :key="item.id || item.table_column.id + '' + item.native_name"
        :label="item.table_column.name"
      >
        <a-input
          :default-value="item.native_name"
          @blur="e => handleBlur(item, e.target.value)"
        />
      </a-form-model-item>
    </template>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";
import SelectSearch from "@/components/SelectSearch.vue";

export default {
  components: {
    SelectSearch
  },
  data() {
    return {
      tableId: "",
      tableName: ""
    };
  },
  computed: {
    ...mapState({
      localeId: store => store.locale.selected.id,
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn,
      table_columnAll: store => store.table_column.all
    }),
    languages() {
      const languages = _.filter(this.languageTableColumn.all.data, {
        table: { name: this.tableName }
      });
      return _.map(this.table_columnAll.data, table_column => ({
        ..._.find(languages, { table_column: { name: table_column.name } }),
        table_column
      }));
    }
  },
  methods: {
    ...mapMutations("languageTableColumn", {
      setValues: "setValues"
    }),
    ...mapActions("languageTableColumn", {
      save: "save"
    }),
    ...mapActions("table_column", {
      getTableColumn: "getAll"
    }),
    handleBlur(language, native_name) {
      if (native_name && language.native_name !== native_name) {
        const formData = { native_name };
        if (language.id) {
          formData.id = language.id;
        } else {
          formData.table_column = language.table_column.id;
          formData.locale = this.localeId;
        }
        this.save(formData).then(() => {
          this.setValues({
            ...this.languageTableColumn.values,
            [`${this.tableName}.${language.table_column.name}`]: native_name
          });
          this.$notification.success({
            message: this.languageBase.save,
            description: this.languageBase.successful
          });
        });
      }
    },
    tableChange(tableId, table) {
      if (tableId) {
        this.tableName = table.name;
        this.getTableColumn({
          table: { filters: [[["table", "=", tableId]]] }
        });
      }
    }
  }
};
</script>
