import _ from "lodash";
import { getSessionStorageItem, setSessionStorageItem, crypto } from "@/utils";
import { SID } from "@/dict";
import { request } from "./request";

const paramsHash = params => {
  delete params.sync;
  return crypto.MD5(JSON.stringify({ ...params, SID })).toString();
};
const createSSName = params => `q-${params.table.name}-${paramsHash(params)}`;

const requestCache = {};
const query = (type, data) =>
  new Promise((resolve, reject) => {
    const sync = data.sync;
    const key = createSSName(data);

    if (!sync) {
      const value = getSessionStorageItem(key);
      if (!_.isEmpty(value)) {
        return resolve(value);
      }
    }

    let req = requestCache[key];
    if (!req) {
      // 缓存
      requestCache[key] = request.post(`/query/${type}`, data).then(res => {
        setSessionStorageItem(key, res);
        return res;
      });
      req = requestCache[key];
    }

    req
      .then(resolve)
      .catch(reject)
      .finally(() => {
        // 删除缓存
        delete requestCache[key];
      });
  });

export const queryAll = data => query("all", data);
export const queryList = data => query("list", data);
export const save = data =>
  request.post("/save", data).then(res => {
    _.forOwn(sessionStorage, (value, key) => {
      const RE = new RegExp(`^q-${data.table.name}-`);
      RE.test(key) && sessionStorage.removeItem(key);
    });
    return res;
  });
