<template>
  <a-row type="flex" justify="space-between">
    <a-col :span="11">
      <a-form-model-item
        prop="params.table_related_id"
        :label="languageTable.table_related"
      >
        <SelectSearch
          v-model="value.table_related_id"
          tableName="table_related"
          textKey="comment"
          :params="params"
          @selected="handleTableRelatedSelect"
          @change="change"
        />
      </a-form-model-item>
    </a-col>
    <a-col :span="11">
      <a-form-model-item
        prop="params.index"
        :label="`${languageBase.table} - ${languageBase.field}`"
      >
        <a-select
          v-model="value.index"
          :disabled="!value.table_related_id"
          :options="indexOptions"
          @change="change"
        />
      </a-form-model-item>
    </a-col>
  </a-row>
</template>

<script>
import { mapState } from "vuex";
import SelectSearch from "@/components/SelectSearch.vue";

export default {
  components: {
    SelectSearch
  },
  props: {
    value: Object
  },
  data() {
    return {
      params: {
        left_join_tables: [
          {
            name: "table_column",
            as_name: "t1_column",
            no_default_field: true,
            fields: ["name"],
            on: ["id", "table_related", "t1_column"]
          },
          {
            name: "table",
            as_name: "t1",
            no_default_field: true,
            fields: ["name"],
            on: ["id", "t1_column", "table"]
          },
          {
            name: "table_column",
            as_name: "t2_column",
            no_default_field: true,
            fields: ["name"],
            on: ["id", "table_related", "t2_column"]
          },
          {
            name: "table",
            as_name: "t2",
            no_default_field: true,
            fields: ["name"],
            on: ["id", "t2_column", "table"]
          }
        ]
      },
      indexOptions: []
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  methods: {
    handleTableRelatedSelect(item) {
      if (item) {
        this.indexOptions = [
          { value: 1, label: `${item.t1.name} / ${item.t1_column.name}` },
          { value: 2, label: `${item.t2.name} / ${item.t2_column.name}` }
        ];
      } else {
        this.value.index = undefined;
        this.indexOptions = [];
      }
    },
    change() {
      this.$emit("input", { ...this.value });
    }
  }
};
</script>
