<template>
  <a-dropdown placement="bottomCenter">
    <a class="ant-dropdown-link" style="margin:0 20px;line-height: 32px">
      <a-row type="flex" align="middle">
        <template>
          <a-avatar v-if="userInfo.avatar" :src="userInfo.avatar | image" />
          <a-avatar v-else icon="user" style="background: #1890ff;" />
        </template>
        <span style="margin-left:8px">
          {{ userInfo.name }}
        </span>
      </a-row>
    </a>

    <a-menu slot="overlay">
      <a-menu-item>
        <a :href="'/' | accountF" style="padding:8px 40px">
          <a-icon type="appstore" />
          {{ languageBase.my_system }}
        </a>
      </a-menu-item>
      <a-menu-item>
        <a :href="'/user' | accountF" style="padding:8px 40px">
          <a-icon type="user" />
          {{ languageBase.personal_center }}
        </a>
      </a-menu-item>
      <a-menu-item>
        <a :href="'/loginList' | accountF" style="padding:8px 40px">
          <a-icon type="login" />
          {{ languageBase.login_logs }}
        </a>
      </a-menu-item>
      <a-menu-divider />
      <a-menu-item>
        <a-button block type="link" icon="logout" size="small" @click="logout">
          {{ languageBase.logout }}
        </a-button>
      </a-menu-item>
    </a-menu>
  </a-dropdown>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { getAccountTokenId } from "@/utils";
import { logout as replaceLogout } from "@/router/func";
import { logout } from "@/api";

export default {
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      userInfo: store => store.user.detail.data
    })
  },
  beforeMount() {
    this.getUserInfo({
      table: { filters: [[["id", "=", getAccountTokenId()]]] }
    });
  },
  methods: {
    ...mapActions("user", {
      getUserInfo: "getDetail",
      removeCache: "removeCache"
    }),
    logout() {
      logout().finally(() => {
        this.removeCache();
        replaceLogout();
      });
    }
  }
};
</script>
