<template>
  <a-card :bordered="false" style="margin:16px 24px">
    <Table
      storeModule="locale"
      :columns="columns"
      :filters="filters"
      @save="getLocale({ sync: 1 })"
    />
  </a-card>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Table from "@/components/Table.vue";

export default {
  components: {
    Table
  },
  data() {
    return {
      filters: {
        code: { operator: "LIKE" },
        native_name: { operator: "LIKE" },
        state: {}
      },
      columns: [
        {
          title: "ID",
          dataIndex: "id",
          sorter: true
        },
        {
          title: "Code",
          dataIndex: "code"
        },
        {
          title: "Native name",
          dataIndex: "native_name"
        },
        {
          title: "Updated At",
          dataIndex: "updated_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "State",
          dataIndex: "state",
          scopedSlots: { customRender: "state" },
          sorter: true
        }
      ]
    };
  },
  computed: {
    ...mapState({
      languageTable: store => store.languageTable.values
    })
  },
  methods: {
    ...mapActions("locale", {
      getLocale: "getAll"
    })
  },
  metaInfo() {
    return {
      title: this.languageTable.locale
    };
  }
};
</script>
