<template>
  <a-form-model
    v-bind="{ labelCol: { span: 6 }, wrapperCol: { span: 18 } }"
    @submit.prevent="handleSubmit"
  >
    <a-row :gutter="24">
      <a-col
        v-for="(item, key) in items"
        :key="key"
        :xs="24"
        :sm="12"
        :lg="8"
        :xxl="6"
      >
        <a-form-model-item v-if="key === 'state'" :label="handleLabel(key)">
          <a-select v-model="formData[key]" allowClear>
            <a-select-option :value="1">
              {{ languageBase.enable }}
            </a-select-option>
            <a-select-option :value="0">
              {{ languageBase.disable }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item v-else :label="handleLabel(key)">
          <a-input v-model="formData[key]" allowClear />
        </a-form-model-item>
      </a-col>
    </a-row>
    <a-row type="flex" justify="end" style="margin-bottom:24px">
      <a-button
        type="primary"
        html-type="submit"
        icon="search"
        :loading="loading"
      >
        {{ languageBase.search || "Search" }}
      </a-button>
      <a-button :style="{ marginLeft: '8px' }" @click="handleReset">
        {{ languageBase.reset }}
      </a-button>
    </a-row>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";

export default {
  props: {
    tableName: String,
    items: Object,
    loading: Boolean
  },
  data() {
    return {
      formData: {}
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values
    })
  },
  methods: {
    handleLabel(key) {
      let lable = this.languageTableColumn[`${this.tableName}.${key}`];
      if (
        !lable &&
        /^(name|state|created_at|updated_at|comment|icon)$/.test(key)
      ) {
        lable = this.languageBase[key];
      }
      return lable || key;
    },
    handleSubmit() {
      const filters = [];
      _.forEach(this.formData, (value, key) => {
        if (!(value === undefined || value === "")) {
          filters.push(this.createFilte(key, value));
        }
      });

      this.$emit("submit", filters);
    },
    createFilte(key, value) {
      const { operator } = this.items[key];
      if (operator === "LIKE") {
        value = `%${value}%`;
      }
      return [key, operator || "=", value];
    },
    handleReset() {
      this.formData = {};
      this.$emit("submit");
    }
  }
};
</script>
