<template>
  <a-config-provider :locale="locale">
    <a-layout style="min-height:100vh">
      <Sider
        v-if="!miniSider"
        @collapse="collapsed => (visiblePageTitle = collapsed)"
      />

      <template v-else>
        <a-drawer
          placement="left"
          :closable="false"
          :visible="visibleSiderDrawer"
          :width="200"
          :bodyStyle="{
            display: 'flex',
            padding: 0,
            minHeight: '100vh'
          }"
          @close="visibleSiderDrawer = false"
        >
          <Sider :collapsible="false" />
        </a-drawer>
      </template>

      <div class="main-right">
        <a-layout>
          <Header>
            <a
              v-show="miniSider"
              class="sider-unfold"
              @click.prevent="visibleSiderDrawer = true"
            >
              <a-icon type="menu-unfold" />
            </a>
            <span class="ant-page-header-heading-title">{{ pageTitle }}</span>
          </Header>

          <a-layout-content>
            <router-view />
            <a-back-top />
          </a-layout-content>
          <a-layout-footer style="text-align: center">
            OOS CC v.{{ APP_VERSION }} ©2020 Created by saileidigi
          </a-layout-footer>
        </a-layout>
      </div>
    </a-layout>
  </a-config-provider>
</template>

<script>
import { mapState } from "vuex";
import _ from "lodash";
import moment from "moment";
import Header from "./Header.vue";
import Sider from "./Sider.vue";

export default {
  components: {
    Header,
    Sider
  },
  data() {
    const miniSider = document.body.offsetWidth <= 768;
    return {
      APP_VERSION: process.env.APP_VERSION,
      locale: _.cloneDeep(window.antd.locales.zh_CN),
      visibleSiderDrawer: false,
      miniSider,
      visiblePageTitle: miniSider
    };
  },
  computed: {
    ...mapState({
      localeCode: store => store.locale.selected.code,
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    }),
    pageTitle() {
      if (!this.visiblePageTitle) return "";
      return (
        this.languageTable[this.$route.name] ||
        this.languageBase[this.$route.name] ||
        this.$route.name
      );
    }
  },
  watch: {
    localeCode() {
      this.handleLocale();
    },
    $route() {
      this.visibleSiderDrawer = false;
    }
  },
  created() {
    this.handleLocale();
  },
  methods: {
    handleLocale() {
      if (this.localeCode) {
        this.locale = _.cloneDeep(
          window.antd.locales[this.localeCode] || window.antd.locales.zh_CN
        );

        moment.locale(this.locale.locale);
      }
    }
  },
  metaInfo() {
    return {
      htmlAttrs: {
        lang: this.locale.locale
      }
    };
  }
};
</script>

<style lang="stylus">
.sider-unfold
  display flex
  align-items center
  justify-content center
  width 64px
  height 64px
  font-size 32px

@media screen and (min-height: 600px)
  .main-right
    overflow auto
    height 100vh
</style>
