<template>
  <a-collapse :style="{ margin: '16px 24px' }" default-active-key="application">
    <a-collapse-panel
      v-for="(types, key) in icons"
      :key="key"
      :header="title[key]"
    >
      <a-row type="flex">
        <a-col v-for="type in types" :key="type">
          <a-card
            :style="{ margin: '10px', width: '180px' }"
            :bodyStyle="{ textAlign: 'center' }"
            :bordered="false"
            hoverable
          >
            <a-icon
              :type="type"
              :style="{ fontSize: '40px', marginBottom: '8px' }"
            />
            <div>{{ type }}</div>
          </a-card>
        </a-col>
      </a-row>
    </a-collapse-panel>
  </a-collapse>
</template>

<script>
import { mapState } from "vuex";
import { titles, icons } from "@/components/icon/data";

export default {
  data() {
    return {
      titles,
      icons
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      localeId: store => store.locale.selected.id
    }),
    title() {
      return this.titles[this.localeId] || this.titles[50];
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.icon
    };
  }
};
</script>
