<template>
  <a-form-model :label-col="{ span: 6 }" :wrapper-col="{ span: 15 }">
    <a-spin v-if="tableAll.loading" />
    <template v-else>
      <template v-for="(item, index) in tableAll.data">
        <a-form-model-item
          :key="index + (languageTable[item.name] || '')"
          :label="item.name"
        >
          <a-input
            :default-value="languageTable[item.name]"
            @blur="e => handleBlur(item, e.target.value)"
          />
        </a-form-model-item>
      </template>
    </template>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  computed: {
    ...mapState({
      localeId: store => store.locale.selected.id,
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      languageTableAllData: store => store.languageTable.all.data,
      tableAll: store => store.table.all
    })
  },
  created() {
    this.getTableAll();
  },
  methods: {
    ...mapMutations("languageTable", {
      setValues: "setValues"
    }),
    ...mapActions("languageTable", {
      save: "save"
    }),
    ...mapActions("table", {
      getTableAll: "getAll"
    }),
    handleBlur(table, native_name) {
      const oldData =
        _.find(this.languageTableAllData, { table: { id: table.id } }) || {};

      if (!native_name || oldData.native_name === native_name) return;

      const formData = { native_name };
      if (oldData.id) {
        formData.id = oldData.id;
      } else {
        formData.table = table.id;
        formData.locale = this.localeId;
      }

      this.save(formData).then(() => {
        this.setValues({
          ...this.languageTable,
          [table.name]: native_name
        });
        this.$notification.success({
          message: this.languageBase.save,
          description: this.languageBase.successful
        });
      });
    }
  }
};
</script>
