<template>
  <a-card :bordered="false" style="margin:16px 24px">
    <Edit ref="edit" />

    <Table
      create
      edit
      storeModule="table_related"
      :columns="columns"
      :filters="filters"
      @show-edit="data => $refs.edit.show(data)"
    >
      <template v-slot:column1="{ record }">
        {{ record.t1.name }} - {{ record.t1_column.name }}
      </template>
      <template v-slot:column2="{ record }">
        {{ record.t2.name }} - {{ record.t2_column.name }}
      </template>
    </Table>
  </a-card>
</template>

<script>
import { mapState } from "vuex";
import Table from "@/components/Table.vue";
import Edit from "./Edit.vue";

export default {
  components: {
    Table,
    Edit
  },
  data() {
    return {
      filters: {
        comment: { operator: "LIKE" },
        state: {}
      },
      columns: []
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  watch: {
    languageBase() {
      this.handleColumns();
    }
  },
  mounted() {
    this.handleColumns();
  },
  methods: {
    handleColumns() {
      this.columns = [
        {
          title: "ID",
          dataIndex: "id",
          sorter: true
        },
        {
          title: "Comment",
          dataIndex: "comment"
        },
        {
          title: `${this.languageBase.table} 1 - ${this.languageBase.show} ${this.languageBase.field}`,
          dataIndex: "t1_column.id",
          scopedSlots: { customRender: "column1" }
        },
        {
          title: `${this.languageBase.table} 2 - ${this.languageBase.show} ${this.languageBase.field}`,
          dataIndex: "t2_column.id",
          scopedSlots: { customRender: "column2" }
        },
        {
          title: "Created At",
          dataIndex: "created_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "Updated At",
          dataIndex: "updated_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        {
          title: "State",
          dataIndex: "state",
          scopedSlots: { customRender: "state" },
          sorter: true
        }
      ];
    }
  },
  metaInfo() {
    return {
      title: this.languageTable.table_related
    };
  }
};
</script>
