<template>
  <a-layout-sider
    v-model="collapsed"
    :collapsible="collapsible"
    @collapse="handleCollapse"
  >
    <a-row
      type="flex"
      align="middle"
      justify="center"
      style="height:64px;flex-flow: nowrap"
    >
      <img alt="OOS logo" src="@/assets/logo.svg" style="height:50px" />
      <h3 class="logo-text">
        OOS CC
      </h3>
    </a-row>

    <a-menu theme="dark" :selected-keys="selectedKeys" mode="inline">
      <template v-for="item in routes">
        <a-menu-item v-if="item.sider !== false" :key="item.name">
          <router-link :to="{ name: item.name }">
            <a-icon :type="item.icon" />
            <span>{{ item.meta.title }}</span>
          </router-link>
        </a-menu-item>
      </template>
    </a-menu>
  </a-layout-sider>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { routes } from "@/router";

export default {
  props: {
    collapsible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      routes,
      selectedKeys: [],
      collapsed: !this.collapsible
        ? false
        : document.body.offsetWidth < 992 && document.body.offsetWidth > 768
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  watch: {
    languageBase() {
      this.handleRoutes();
    },
    languageTable() {
      this.handleRoutes();
    },
    "$route.name"(val) {
      this.selectedKeys = [val];
    }
  },
  created() {
    this.handleRoutes();
  },
  mounted() {
    this.handleCollapse();
    this.selectedKeys = [this.$route.name];
  },
  methods: {
    handleRoutes() {
      _.forEach(this.routes, item => {
        item.meta.title =
          this.languageTable[item.name] ||
          this.languageBase[item.name] ||
          item.meta.title;
      });
      this.routes = [...this.routes];
    },
    handleCollapse() {
      this.$emit("collapse", this.collapsed);
    }
  }
};
</script>

<style lang="stylus">
.ant-layout-sider
  .logo-text
    margin 0 0 0 8px
    width 80px
    opacity 1
    transition all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1)
    white-space nowrap
    color #fff

.ant-layout-sider-collapsed
  .logo-text
    margin 0
    width 0
    opacity 0
</style>
