<template>
  <a-drawer
    :title="title"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    @close="close"
    :headerStyle="{ textAlign: 'center' }"
  >
    <a-form-model
      v-if="visible"
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item prop="name" :label="languageBase.name">
        <a-input v-model="formData.name" auto-focus />
      </a-form-model-item>
      <a-form-model-item :label="languageBase.icon">
        <IconSelect v-model="formData.icon" />
      </a-form-model-item>
      <a-form-model-item prop="comment" :label="languageBase.comment">
        <a-textarea v-model="formData.comment" />
      </a-form-model-item>
      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
          :disabled="disabledSubmit"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";
import { exist } from "@/api";
import IconSelect from "@/components/icon/Select.vue";

const defaultFormData = { name: "", comment: "", icon: "" };
const formDataKeys = _.keys(defaultFormData);

export default {
  components: { IconSelect },
  data() {
    const validateInputRequired = (rule, value, callback) => {
      value === "" ? callback(this.languageBase.please_input) : callback();
    };

    const validateNameExist = (rule, value, callback) => {
      if (validateNameExist.val === value || this.oldFormData.name === value) {
        callback();
      } else {
        validateNameExist.val = value;
        exist({ table_name: "menu_group", filters: { name: value } }).then(
          res => {
            res ? callback(this.languageBase.existed) : callback();
          }
        );
      }
    };

    return {
      visible: false,
      title: "",
      width: document.body.offsetWidth < 768 ? document.body.offsetWidth : 768,
      submitLoding: false,
      formData: { ...defaultFormData },
      rules: {
        name: [
          { required: true, validator: validateInputRequired },
          { min: 1, max: 50, trigger: "blur" },
          { validator: validateNameExist, trigger: "blur" }
        ],
        comment: [{ min: 1, max: 255, trigger: "blur" }]
      },
      oldFormData: { ...defaultFormData }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageMenuGroup: store => store.languageMenuGroup.values
    }),
    disabledSubmit() {
      return _.isEmpty(
        _.pickBy(this.oldFormData, (value, key) => this.formData[key] !== value)
      );
    }
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm && this.$refs.ruleForm.clearValidate();
    }
  },
  mounted() {
    this.handleRules();
  },
  methods: {
    ...mapMutations("languageMenuGroup", {
      setLanguageMenuGroupValues: "setValues"
    }),
    ...mapActions("menu_group", {
      save: "save"
    }),
    show(data) {
      if (data) {
        this.title = this.languageBase.edit;
        this.formData = _.pick(data, [...formDataKeys, "id"]);
      } else {
        this.title = this.languageBase.create;
        this.formData = { ...defaultFormData };
      }

      this.rules.name[2].validator.val = "";
      this.oldFormData = _.pick(this.formData, formDataKeys);
      this.visible = true;
    },
    close() {
      this.visible = false;
      this.$refs.ruleForm.clearValidate();
    },
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;

        let formData = _.pick(this.formData, [...formDataKeys, "id"]);
        formData = _.pickBy(
          formData,
          (value, key) => value !== this.oldFormData[key]
        );

        if (_.isEmpty(formData)) return;

        this.save(formData).then(() => {
          if (formData.id && formData.name) {
            _.forOwn(sessionStorage, (value, key) => {
              /^q-language_menu_group-/.test(key) &&
                sessionStorage.removeItem(key);
            });
            this.setLanguageMenuGroupValues({
              ...this.languageMenuGroup,
              [formData.name]: this.languageMenuGroup[this.oldFormData.name]
            });
          }

          this.$notification.success({
            message: this.languageBase.save,
            comment: this.languageBase.successful
          });
          this.close();
        });
      });
    },
    handleRules() {
      const compiled = _.template(this.languageBase.character_template);
      this.rules.name[1].message = compiled(this.rules.name[1]);
      this.rules.comment[0].message = compiled(this.rules.comment[0]);
    }
  }
};
</script>
