import _ from "lodash";
import Vue from "vue";
import VueRouter from "vue-router";
import { SID, origins } from "@/dict";
import {
  delToken,
  getTokenUid,
  getAccountToken,
  setAccountToken,
  getTokenPayload
} from "@/utils";
import { goLogin, handleLogin } from "./func";
import Main from "@/components/Main.vue";
import Language from "@/views/language/Index.vue";
import Locale from "@/views/locale/List.vue";
import DataType from "@/views/dataType/List.vue";
import Table from "@/views/table/List.vue";
import TableRelated from "@/views/tableRelated/List.vue";
import Icons from "@/views/icons/Index.vue";
import MenuGroup from "@/views/menuGroup/List.vue";
import Page404 from "@/views/404.vue";

!SID && window.location.replace(origins.ACCOUNT_FRONTEND);

Vue.use(VueRouter);

export const routes = [
  {
    name: "language",
    path: "/language",
    component: Language,
    meta: { title: "语言" },
    icon: "font-colors"
  },
  {
    name: "locale",
    path: "/locale",
    component: Locale,
    meta: { title: "区域" },
    icon: "global"
  },
  {
    name: "icon",
    path: "/icon",
    component: Icons,
    meta: { title: "图标" },
    icon: "smile"
  },
  {
    name: "menu_group",
    path: "/menu_group",
    component: MenuGroup,
    meta: { title: "菜单组" },
    icon: "menu"
  },
  {
    name: "data_type",
    path: "/data_type",
    component: DataType,
    meta: { title: "数据类型" },
    icon: "number"
  },
  {
    name: "table",
    path: "/table",
    component: Table,
    meta: { title: "数据表" },
    icon: "table"
  },
  {
    name: "table_related",
    path: "/table_related",
    component: TableRelated,
    meta: { title: "多对多关联表" },
    icon: "swap"
  }
];

const router = new VueRouter({
  mode: "history",
  base: `/${SID}`,
  routes: [
    {
      path: "/",
      name: "Main",
      component: Main,
      redirect: "/language",
      children: routes
    },
    {
      name: "404",
      path: "*",
      component: Page404
    }
  ]
});

router.beforeEach((to, from, next) => {
  // 有 uid 登录有效
  const uid = getTokenUid(SID);

  if (to.query.auth) {
    if (to.query.auth !== getAccountToken()) {
      // 新 token ，初始化尝试登录次数
      localStorage.setItem("try_to_login_num", 0);
    }

    // 保存 auth 。 auth 有效 并且 有uid，对比是不是同一个用户，不是则删除登录token
    if (setAccountToken(to.query.auth) && uid) {
      const { id } = getTokenPayload(to.query.auth);
      if (id && uid !== id) {
        delToken(SID);
      }
    }

    // 删除auth
    router.replace(
      _.pick(
        {
          ...to,
          query: _.omit(to.query, ["auth"])
        },
        ["name", "params", "query"]
      )
    );
  } else if (!uid) {
    const auth = getAccountToken();
    !auth ? goLogin() : handleLogin(auth, next);
  } else {
    next();
  }
});

export default router;
