import _ from "lodash";
import { queryList, save } from "@/api";
import { storeOptions } from "./default";

const tableName = "data_type";
const options = new storeOptions({
  list: {
    api: queryList,
    params: {
      table: {
        name: tableName,
        fields: ["name", "comment", "auth", "params", "component"]
      }
    },
    beforeSet: res => {
      _.forEach(res.data, item => {
        item.params = item.params ? JSON.parse(item.params) : {};
      });
    }
  },
  save: {
    api: save,
    tableName,
    beforeSet: record => {
      if (record.params) {
        record.params = JSON.parse(record.params);
      }
    }
  }
});

export default {
  namespaced: true,
  ...options
};
