<template>
  <a-form-model :label-col="{ span: 6 }" :wrapper-col="{ span: 15 }">
    <a-spin v-if="menuGroupAll.loading" />
    <template v-else>
      <template v-for="(item, index) in menuGroupAll.data">
        <a-form-model-item
          :key="index + (languageMenuGroup[item.name] || '')"
          :label="item.name"
        >
          <a-input
            :default-value="languageMenuGroup[item.name]"
            @blur="e => handleBlur(item, e.target.value)"
          />
        </a-form-model-item>
      </template>
    </template>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  computed: {
    ...mapState({
      localeId: store => store.locale.selected.id,
      languageBase: store => store.languageBase.values,
      languageMenuGroup: store => store.languageMenuGroup.values,
      languageMenuGroupAllData: store => store.languageMenuGroup.all.data,
      menuGroupAll: store => store.menu_group.all
    })
  },
  created() {
    this.getMenuGroupAll();
  },
  methods: {
    ...mapMutations("languageMenuGroup", {
      setValues: "setValues"
    }),
    ...mapActions("languageMenuGroup", {
      save: "save"
    }),
    ...mapActions("menu_group", {
      getMenuGroupAll: "getAll"
    }),
    handleBlur(menu_group, native_name) {
      const oldData =
        _.find(this.languageMenuGroupAllData, {
          menu_group: { id: menu_group.id }
        }) || {};

      if (!native_name || oldData.native_name === native_name) return;

      const formData = { native_name };
      if (oldData.id) {
        formData.id = oldData.id;
      } else {
        formData.menu_group = menu_group.id;
        formData.locale = this.localeId;
      }

      this.save(formData).then(() => {
        this.setValues({
          ...this.languageMenuGroup,
          [menu_group.name]: native_name
        });
        this.$notification.success({
          message: this.languageBase.save,
          description: this.languageBase.successful
        });
      });
    }
  }
};
</script>
